.sidebar{
  background-color: #273142;
  bottom: 0;
  left: 0;
  overflow-x: hidden;
  overflow-y: auto;
  padding: 0;
  position: fixed;
  top: 86px;
}
.btn.btn-home{
  background-color: #22569e;
  background-image: url("data:image/svg+xml;base64,");
  background-position: 20px center;
  background-repeat: no-repeat;
  background-size: 20px 20px  ;
  border-radius: 0;
  color: #ffffff;
  display: block;
  font-weight: bold;
  height: 60px;
  outline: none;
  padding-left: 50px;
  text-align: left;
  width: 100%;
}
.btn.btn-home:hover,
.btn.btn-home:focus{
  background-color: #1a4a8d;
  cursor: pointer;
  outline: none;
}
.sidebar-intro p{
  color: #ffffff;
  font-size: 12px;
  line-height: 135%;
  margin: 30px 0;
  padding: 0 30px;
}
